@import '../../variables.scss';

.settingsPageContent {
  .horizontalLine {
    height: 1px;
    border-bottom: 1px solid $card-bg-color;
    left: 0;
    right: 0;
    position: sticky;
    margin: 0 -32px;
    @media (max-width: 360px)  {
      margin: 0 -16px;
    }
  }

  .settingsItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 16px;
    padding-bottom: 16px;
    .languageIcon {
      svg {
        margin-right: 28px;
        @media (max-width: 360px)  {
          margin-right: 16px;
        }
        flex-shrink: 0;
        path {
          fill: $primary-color;
        }
      }
    }

    .otherIcon {
      svg {
        margin-right: 28px;
        @media (max-width: 360px)  {
          margin-right: 16px;
        }
        flex-shrink: 0;
        path {
          fill: $primary-color;
          stroke: $primary-color;
        }
      }
    }

    .textContainer {
      display: flex;
      flex-direction: column;
      width: 100%;
      .header {
        font-size: $font-small;
        text-transform: uppercase;
        color: $primary-color;
        font-weight: $semi-bold;
        margin-bottom: 8px;
      }

      .title {
        font-size: $body-text-size;
        color: $title-text-color;
        font-weight: $medium;
      }

      .body {
        font-size: $font-medium;
        font-weight: $medium;
        color: $body-text-color;
        &.actions {
          margin-top: 12px;
          display: flex;
          flex-direction: row;
        }
      }
    }
  }
}
